import {
  Avatar,
  Box,
  Button,
  Center,
  Divider,
  Flex,
  Grid,
  GridItem,
  Image,
  Spacer,
  Stack,
  Table,
  TableContainer,
  Tbody,
  Td,
  Text,
  Th,
  Thead,
  Tr,
} from "@chakra-ui/react";
import React, { useEffect, useMemo } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate, useParams } from "react-router-dom";
import {
  getData,
  getDataByIdSuperAdmin,
} from "../../redux/actions/common.action";
import Loader from "../../components/Loader";
import { FiRefreshCcw } from "react-icons/fi";
import { TfiEye } from "react-icons/tfi";

// Interface for the details of the signed-in user
interface SigninUserDetails {
  signinuser: {
    UserType: number;
  };
}

//interface for the usersLists
interface adminLists {
  admins: {
    Name: string;
    UserName: string;
    UserType: number;
    Email: string;
    Phone: number;
    role: string;
    Branch: string;
    Permission: string;
    City: string;
    image: string;
    serialNumber: number;
    _id: string;
    active: boolean;
  }[];
  loading: boolean;
}

const AdminDetails: React.FC = () => {
  // Retrieve the details of the signed-in user from the Redux store
  //   const { signinuser } = useSelector(
  //     (state: { common: SigninUserDetails }) => state.common
  //   );

  // Accessing necessary hooks, variables, and parameters
  const { id } = useParams();

  // Selecting the users and loading state from the Redux store
  const { admins, loading } = useSelector(
    (state: { common: adminLists }) => state.common
  );
  console.log(admins);

  // Accessing the Redux dispatch function
  const dispatch: any = useDispatch();

  // Accessing the navigation function from react-router-dom
  const Navigate = useNavigate();

  // Setting the endpoint for API requests
  const endpoint: string = "admins";

  // useEffect hook to fetch data when 'id' changes
  useEffect(() => {
    dispatch(getDataByIdSuperAdmin(id, endpoint));
  }, [id]);

  return (
    <Stack h="100%" mt={1}>
      <Box
        px={10}
        h="40px"
        display={"flex"}
        justifyContent={"space-between"}
        alignItems={"center"}
      >
        <Text color={"#000000"} fontSize={"1.25rem"} fontWeight={"600"}>
          Admin Details
        </Text>
        <Spacer />
        <Box mt="2" ml="3">
          <Button
            size={"xs"}
            borderRadius={"full"}
            onClick={() => dispatch(getData(endpoint))}
          >
            <FiRefreshCcw />
          </Button>
        </Box>
      </Box>
      <Divider
        bg="gray.300" // Set the background color to dark gray
        height="2px" // Set the height of the divider
      />
      <Grid
        // h="200px"
        templateRows="repeat(2, 1fr)"
        templateColumns="repeat(5, 1fr)"
        gap={2}
        p={4}
      >
        <GridItem rowSpan={2} colSpan={1} m={"auto"}>
          <Image
            borderRadius="full"
            boxSize="150px"
            src="https://bit.ly/dan-abramov"
            alt="Dan Abramov"
          />
        </GridItem>
        <GridItem colSpan={1}>
          <Flex flexDir={"column"}>
            <Text color={"gray.500"}>Name</Text>
            <Text fontSize={"xl"}>Tarun Nagar</Text>
          </Flex>
        </GridItem>
        <GridItem colSpan={1}>
          <Flex flexDir={"column"}>
            <Text color={"gray.500"}>UserName</Text>
            <Text fontSize={"xl"}>tarunnagar8191</Text>
          </Flex>
        </GridItem>
        <GridItem colSpan={1}>
          <Flex flexDir={"column"}>
            <Text color={"gray.500"}>Phone Number</Text>
            <Text fontSize={"xl"}>9899786756</Text>
          </Flex>
        </GridItem>
        <GridItem colSpan={1}>
          <Flex flexDir={"column"}>
            <Text color={"gray.500"}>Email Id</Text>
            <Text fontSize={"xl"}>tarun91@gmail.com</Text>
          </Flex>
        </GridItem>
        <GridItem colSpan={1}>
          <Flex flexDir={"column"}>
            <Text color={"gray.500"}>City</Text>
            <Text fontSize={"xl"}>Dwarka Mor</Text>
          </Flex>
        </GridItem>
        <GridItem colSpan={1}>
          <Flex flexDir={"column"}>
            <Text color={"gray.500"}>Branch</Text>
            <Text fontSize={"xl"}>Delhi</Text>
          </Flex>
        </GridItem>
      </Grid>

      <Divider
        bg="gray.300" // Set the background color to dark gray
        height="2px" // Set the height of the divider
      />
      <Box
        px={8}
        // h="40px"
        display={"flex"}
        justifyContent={"space-between"}
        alignItems={"center"}
      >
        <Text color={"#000000"} fontSize={"1.25rem"} fontWeight={"600"}>
          User List
        </Text>
        <Spacer />
      </Box>

      <TableContainer px="2">
        <Box overflowY="auto" height={480} maxHeight="480px">
          <Table variant="simple" size="sm">
            <Thead bg={"gray.100"}>
              <Tr>
                <Th>Photo</Th>
                <Th>Name</Th>
                <Th>Username</Th>
                <Th>Phone No.</Th>
                <Th>Email-ID</Th>
                <Th>UserType</Th>
                <Th>City</Th>
                <Th>Branch</Th>
                <Th>Action</Th>
              </Tr>
            </Thead>
            {loading ? (
              <Loader />
            ) : (
              <Tbody>
                {admins?.map((el) => (
                  <Tr key={el._id}>
                    <Td>
                      {" "}
                      <Avatar
                        size="sm"
                        bg="blue.500"
                        color="whiteAlpha.900"
                        name={el.Name}
                        src={el.image}
                      />
                    </Td>
                    <Td>{el.Name}</Td>
                    <Td>{el.UserName}</Td>
                    <Td>{el.Phone}</Td>
                    <Td>{el.Email}</Td>
                    <Td>{el.UserType}</Td>
                    <Td>{el.City}</Td>
                    <Td>{el.Branch}</Td>
                    <Td display={"flex"} alignItems={"center"}>
                      <Box
                        cursor={"pointer"}
                        mr={"5"}
                        onClick={() => Navigate(`/user-details`)}
                      >
                        <TfiEye color="blue" size={19} />
                      </Box>
                    </Td>
                  </Tr>
                ))}
              </Tbody>
            )}
          </Table>
        </Box>
      </TableContainer>
    </Stack>
  );
};

export default AdminDetails;
